import IScroll from './iscroll-probe.js'
var _index_key_ = {};	

var scrollObj = {};

var extend=function(oldObj,newObj){
	for(var key in newObj){
		oldObj[key]=newObj[key];
	}
	return oldObj;
};

var Scroll = function(selector, opts){
	var $scroll;
	var options = {
        probeType: 2,//probeType1probeType2onscrollprobeType3requestAnimationFrameuseTransition  
        scrollbars: 'custom',//  
        mouseWheel: true,//  
        fadeScrollbars: true,//  
        bounce:true,// 
        listenX: false, 
        interactiveScrollbars:true,//  
        shrinkScrollbars:'scale',// 'clip' or 'scale'.  
        click: false ,//   
        momentum:true,//   
		preventDefaultException: { tagName: /^(INPUT|TEXTAREA|BUTTON|SELECT|IMG)$/ }
	};
	options = extend(options, opts||{});

	IScroll.utils.isBadAndroid = false;//
	$scroll = new IScroll(selector, options);

	$scroll.refresh();
	return $scroll
};


export default Scroll;
// document.addEventListener('touchmove', function (e) { e.preventDefault(); }, false);



